<template>
	<div>
		<HeaderComponent :view-title="wheelTitle"></HeaderComponent>
		<div id="wheelBoxMain">
			<section class="announcement clear">
				<div class="announcementTitle left">
					<span>中奖公告:</span>
				</div>
				<div class="announcementContent right">
					<ul class="announcementContentLists clear" :class="{announcementTop:animate}">
						<li class="left" v-for="(item,index) in announcementContentData">
							<span>恭喜</span><span>{{item.name}}</span><span>在</span><span>{{item.activity}}</span><span>活动中抽中</span><span>{{item.prizes}}</span><span>。</span>
						</li>
					</ul>
				</div>
			</section>
			<div class="wheelTitle">
				<h2 class="title">幸运大转盘</h2>
				<div class="subTitle">美多商城周年庆典</div>
			</div>
			<div class="wheelBoxMainWheel">
				<img src="../../assets/images/1.png" id="shan-img" style="display:none;">
			    <img src="../../assets/images/2.png" id="sorry-img" style="display:none;">
				<div class="banner">
					<div class="turnplate">
						<canvas class="item" id="wheelcanvas" width="422px" height="422px" style="transform: rotate(216deg);"></canvas>
						<img class="pointer" src="../../assets/images/turnplate-pointer.png">
					</div>
				</div>
				<section class="lotteryNumber">
					<span>您还有</span><span class="count">6</span><span>次抽奖机会</span>
				</section>
				<section class="countdown">
					<p class="title">活动倒计时</p>
					<div class="countdownBox">
						<span class="tNum">2</span><span>天</span><span class="tNum">3</span><span>小时</span><span class="tNum">45</span><span>秒</span>
					</div>
				</section>
			</div>
			<section class="sponsorIntroduction">	
				<p class="title">主办方介绍</p>
				<p class="content">广州景行广告策划有限公司是一家以开发软件以及网站为主的公司。现特推出大转盘抽奖活动回馈新老客户！诚信为本、质量至上是我们的宗旨，让优秀的品质、合理的价格、完善的服务见证我们的专业和真诚，期待与您更多商务合作！</p>
			</section>
			<section class="activityRules">
				<p class="title">活动规则</p>
				<ul class="activityRulesLists">
					<li>
						<span>活动时间：</span><span>xxx年xx月xx日——xxx年xx月xx日</span>
					</li>
					<li>
						<span>活动规则：</span><span>每个微信号可抽一次，每个微信号可获得一次奖励。</span>
					</li>
					<li>
						<span>奖品说明：</span>
					</li>
					<li>
						<span>一等奖3名：</span><span>xx品牌玩具</span>
					</li>
					<li>
						<span>二等奖5名：</span><span>xx皮包</span>
					</li>
					<li>
						<span>三等奖5名：</span><span>xx拉杆箱</span>
					</li>
					<li>
						<span>本次活动最终解释权归xx所有</span>
					</li>
				</ul>
			</section>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	import {rotate,getRotateAngle,stopRotate} from '../../assets/js/awardRotate';
	import {funHd} from '../../assets/js/zhuanpan';
	

			var turnplate={
				restaraunts:[],				//大转盘奖品名称
				colors:[],					//大转盘奖品区块对应背景颜色
				outsideRadius:192,			//大转盘外圆的半径
				textRadius:155,				//大转盘奖品位置距离圆心的距离
				insideRadius:68,			//大转盘内圆的半径
				startAngle:0,				//开始角度
				
				bRotate:false				//false:停止;ture:旋转
			};

			funHd.turnplate = turnplate
			console.log(funHd);

	export default {
		wheelTitle:Object,
		data(){
			return{
				wheelTitle:{
					name:'幸运大转盘',
					showCode:0
				},
				announcementContentData:[
					{name:'大禹',activity:'免费抽奖',prizes:'88元红包'},
					{name:'明明',activity:'免费抽奖',prizes:'108元红包'},
					{name:'红红',activity:'免费抽奖',prizes:'时尚女包'}
				],
				animate:false
			}
		},
		created(){
			setInterval(this.showAnnouncement,2000);
		},
		mounted(){

			$(document).ready(function(){
				//动态添加大转盘的奖品与奖品区域背景颜色
				turnplate.restaraunts = ["50M免费流量包", "10闪币", "谢谢参与", "5闪币", "10M免费流量包", "20M免费流量包", "20闪币 ", "30M免费流量包", "100M免费流量包", "2闪币"];
				turnplate.colors = ["#FFF4D6", "#FFFFFF", "#FFF4D6", "#FFFFFF","#FFF4D6", "#FFFFFF", "#FFF4D6", "#FFFFFF","#FFF4D6", "#FFFFFF"];

				
				var rotateTimeOut = function (){
					$('#wheelcanvas').rotate({
						angle:0,
						animateTo:2160,
						duration:8000,
						callback:function (){
							alert('网络超时，请检查您的网络设置！');
						}
					});
				};

				funHd.pointer()
			});

		

			//页面所有元素加载完毕后执行drawRouletteWheel()方法对转盘进行渲染
			window.onload=function(){
				funHd.drawRouletteWheel();
			};


		},
		methods:{
			showAnnouncement:function(){
				this.animate = true;
				setTimeout(()=>{
					this.announcementContentData.push(this.announcementContentData[0]);
					this.announcementContentData.shift();
					this.animate = false;
				},500);
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	#wheelBoxMain{
		margin-top:2.2rem;
		background:#f35353;
		//中奖公告
		.announcement{
			width:100%;
			height:1.5rem;
			background:#DB4B4B;
			padding:0 0.5rem;
			.announcementTitle{
				width:18%;
				margin-right:0.2rem;
				span{
					@include sc(0.6rem,#FEFAFA);
				}
			}
			.announcementContent{
				width:80%;
				height:1.5rem;
				overflow:hidden;
				.announcementTop{
					transition: all 0.5s;
            		margin-top: -1.5rem;
				}
				.announcementContentLists{
					li{
						height:1.5rem;
						span{
							@include sc(0.6rem,#FEFAFA);
						}
					}
				}
			}
		}
		//标题
		.wheelTitle{
			text-align: center;
			margin:0.5rem 0;
			.title{
				@include sc(2rem,#FEFAFA);
			}
			.subTitle{
				@include sc(0.6rem,#FEFAFA);
			}
		}
		.wheelBoxMainWheel{
			margin-bottom:1rem;
			.banner{
				display:block;
				width:80%;
				margin-left:auto;
				margin-right:auto;
				margin-bottom: 0.4rem;
				.turnplate{
					display:block;
					width:100%;
					position:relative;
					background: url(../../assets/images/turnplate-bg.png) no-repeat;
					background-size:100% 100%;
				}
				.turnplate canvas.item{width:100%;}
				.turnplate img.pointer{
					position:absolute;
					width:31.5%;
					height:42.5%;
					left:34.6%;
					top:23%;
				}
			}
			.lotteryNumber{
				text-align: center;
				margin-bottom:0.5rem;
				span{
					@include sc(0.6rem,#FEFAFA);
				}
				span.count{
					@include sc(0.8rem,yellow);
				}
			}
			.countdown{
				text-align: center;
				p.title{
					width:3rem;
					height:1rem;
					line-height: 1rem;
					@include sc(0.4rem,#FEFAFA);
					background: #F74448;
					margin:0 auto;
					@include borderRadius(0.2rem);
				}
				.countdownBox{
					width:5rem;
					height:1.6rem;
					margin:0 auto;
					background: #E63434;
					@include borderRadius(0.2rem);
					span{
						@include sc(0.6rem,#FEFAFA);
					}
					.tNum{
						color:yellow;
					}
				}
			}
		}

		.sponsorIntroduction{
			padding:0 0.5rem;
			margin-bottom: 0.5rem;
			p.title{
				@include sc(0.4rem,#FEFAFA);
				text-align: center;
				margin-bottom:0.5rem;
			}
			p.content{
				@include sc(0.4rem,#FEFAFA);
				background:#F56565;
				@include borderRadius(0.2rem);
				padding:0.2rem;
			}
		}
		.activityRules{
			padding:0 0.5rem;
			p.title{
				@include sc(0.4rem,#FEFAFA);
				text-align: center;
				margin-bottom:0.5rem;
			}
			ul.activityRulesLists{
				background:#F56565;
				@include borderRadius(0.2rem);
				padding:0.2rem;
				li{
					height:0.8rem;
					line-height: 0.4rem;
					span{
						@include sc(0.4rem,#FEFAFA);	
					}
				}
			}
		}
	}
</style>


