<template>
	<header id="head-top">
		<svg v-if="isShow == 'true'" class="icon-head" aria-hidden="true"  @click="back()">
	  		<use xlink:href="#icon-jiantouzuo"></use>
		</svg>
		<h4 class="header-title">{{viewTitle.name}}</h4>
	</header>
</template>

<script type="text/javascript">
	export default {
		isShow:Boolean,
		props:{
			viewTitle:Object
		},
		data(){
			return {
				isShow:'true',
			}
		},
		created(){
			console.log(this.viewTitle)
			//判断是否要显示 返回箭头(>)
			if(this.viewTitle.showCode == 0){
				 this.isShow = 'false'
			}
		},
		mounted(){
			
		},
		methods:{
			back:function(){
				this.$router.go('-1');
			},
			
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../style/mixin';
	.icon-head {
	   width: 1rem; height: 1rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#333;
	   @include ct;
	   left:0.25rem;
	}
	#head-top{
		width:100%;
		height:2.2rem;
		line-height:2.2rem;
		background:#fff;
		position:fixed;
		top:0;
		left:0;
		z-index:999;
		.header-title{
			text-align:center;
		}
	}
</style>


